<template>
  <div>
    <v-dialog
      :value="show"
      max-width="950"
      @click:outside="$emit('close')"
    >
      <v-card>
        <v-card-title class="headline i18n word-break">{{ $vuetify.lang.t('$vuetify.home.legal.title') }}</v-card-title>

        <div class="container">
          <span class="mb-2">{{ $vuetify.lang.t('$vuetify.home.legal.description') }}</span>
        </div>

        <div class="container">
          <v-expansion-panels>
            <v-expansion-panel
              v-for="(item,i) in items"
              :key="i"
            >
              <v-expansion-panel-header><strong>{{ item.title }}</strong></v-expansion-panel-header>
              <v-expansion-panel-content><div class="markdown" v-html="item.mdDesc"/></v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>

          <v-checkbox v-model="legalsAccepted" color="success" :label="$vuetify.lang.t('$vuetify.home.legal.txtCheckbox')"></v-checkbox>
        </div>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            :disabled="!legalsAccepted"
            @click="$emit('acceptedLegal')"
          >
            {{ $vuetify.lang.t('$vuetify.home.legal.btn.continue') }}
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            {{ $vuetify.lang.t('$vuetify.home.legal.btn.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import marked from 'marked'

export default {
  name: 'markdown-content-dialog',
  props: {
    show: Boolean
  },
  data: () => ({
    items: [],
    legalsAccepted: false
  }),
  created() {
    this.items = [
      {
        title: this.$vuetify.lang.t('$vuetify.home.legal.agb.title'),
        mdDesc: marked(this.$vuetify.lang.t('$vuetify.home.legal.agb.mdDesc'))
      }, {
        title: this.$vuetify.lang.t('$vuetify.home.legal.dsgvo.title'),
        mdDesc: marked(this.$vuetify.lang.t('$vuetify.home.legal.dsgvo.mdDesc'))
      }
    ]
  }
}
</script>

